.app__menuitem{
    width: 100%;
    margin: 1rem 0rem;

    display: flex;
    flex-direction: column;
}

.app__menuitem-head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}

.app__menuitem-tag{
    width: 100%;
    margin-top: .2rem;
}

.app__menuitem-name{
    flex: 1;
}

.app__menuitem-dash {
    width: 90px;
    height: 1px;
    background: var(--color-golden);
    margin: 0rem 1rem;
}

.app__menuitem-price{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}