.app__aboutus{
    position: relative;
}

.app__aboutus-overlay{
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 390px;
    height: 415px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 1;
}

.app__aboutus-content-left {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}

.app__aboutus-content-img {
    margin: 2rem 4rem;
}

.app__aboutus-content-img img {
    height: 910px;
}

.app__aboutus-content-right {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
}

.app__aboutus-content-right p,
.app__aboutus-content-left p{
    margin: 2rem 0rem;
    color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
    .app__aboutus-content-img img{
        height: 1110px;
    }
    .app__aboutus-content-right p,
    .app__aboutus-content-left p{
        margin: 4rem 0rem;
    }

}

@media screen and (max-width: 900px) {
    .app__aboutus-content{
        flex-direction: column;
    }
    .app__aboutus-content-img{
        margin: 4rem 0rem;
    }
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img{
        width: 80%;
        height: 320px;
    }
}